$fonts: '~@renproject/fonts/fonts';

@font-face {
    font-family: 'SuisseIntl';
    src: url('#{$fonts}/SuisseIntl/SuisseIntl-Thin.otf') format('opentype');
    font-weight: 100;
    font-style: normal;
}

@font-face {
    font-family: 'SuisseIntl';
    src: url('#{$fonts}/SuisseIntl/SuisseIntl-Light.otf') format('opentype');
    font-weight: 200;
    font-style: normal;
}

@font-face {
    font-family: 'SuisseIntl';
    src: url('#{$fonts}/SuisseIntl/SuisseIntl-Book.otf') format('opentype');
    font-weight: 300;
    font-style: normal;
}

@font-face {
    font-family: 'SuisseIntl';
    src: url('#{$fonts}/SuisseIntl/SuisseIntl-Regular.otf') format('opentype');
    font-weight: 400;
    font-style: normal;
}

@font-face {
    font-family: 'SuisseIntl';
    src: url('#{$fonts}/SuisseIntl/SuisseIntl-Bold.otf') format('opentype');
    font-weight: 700;
    font-style: normal;
}

@font-face {
    font-family: 'SuisseIntl';
    src: url('#{$fonts}/SuisseIntl/SuisseIntl-Black.otf') format('opentype');
    font-weight: 800;
    font-style: normal;
}


// Override PingFang with SuisseIntl
// Any project that is still using PingFang will use SuisseIntl instead

@font-face {
    font-family: 'PingFang SC';
    src: url('#{$fonts}/SuisseIntl/SuisseIntl-Light.otf') format('opentype');
    font-weight: 200;
    font-style: normal;
}

@font-face {
    font-family: 'PingFang SC';
    src: url('#{$fonts}/SuisseIntl/SuisseIntl-Regular.otf') format('opentype');
    font-weight: 400;
    font-style: normal;
